.main-button {
  font-weight: 700;
  width: max-content;
  padding-right: 5px;
  text-transform: uppercase;
  text-align: center;
  text-decoration: none;
  display: block;
  margin-top: 20px;
  font-size: 16px;
  cursor: pointer;

}

.project-button{
  display: flex;
  justify-content: center;
  margin-top: 20px;
}
.project-button > .main-button{
  margin-right: 0 !important;
}
/* Media Query */
@media (max-width: 768px) {
  .main-button {
    font-size: 15px;
    padding: 12px 18px;
    margin-right: 0px;
  }
}
@media (max-width: 320px) {
  .main-button {
    font-size: 12px;
  }
}
